/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import cn from 'classnames';
import styles from './styles.module.scss';
import switchIcon from '../../assets/images/switch_icon.png';
import questionIcon from '../../assets/images/question_icon.png';
import { calendarData, dividData } from '../../data';

const initialState = {
  calend: {
    table: calendarData,
    active: null,
    sort: null,
  },
  divid: {
    table: dividData,
    active: null,
    sort: null,
  },
};

const dividendHeaderData = [
  { title: 'Инструмент', connectedField: 'name', id: 0 },
  { title: 'Купить до', connectedField: 'buyBefore', id: 1 },
  { title: 'Закрытие реестра', connectedField: 'lastDate', id: 2 },
  { title: 'Размер купона', connectedField: 'couponValue', id: 3 },
  { title: 'Цена', connectedField: 'price', id: 4 },
  { title: 'Пустое поле', connectedField: 'addField1', id: 5 },
  { title: 'Пустое поле', connectedField: 'addField2', id: 6 },
];

const reportHeaderData = [
  { title: 'Инструмент', connectedField: 'name', id: 10 },
  { title: 'Дата отчёта', connectedField: 'reportDate', id: 11 },
  { title: 'Вид отчёта', connectedField: 'reportType', id: 12 },
  { title: 'Прогноз', connectedField: 'forecast', id: 13 },
  { title: 'Факт', connectedField: 'fact', id: 14 },
  { title: 'Пустое поле', connectedField: 'addField1', id: 15 },
  { title: 'Пустое поле', connectedField: 'addField2', id: 16 },
];
class EventCalendar extends React.Component {
  state = initialState;
  clickHandler = (name, currenTable) => () => {
    const data = this.state[currenTable];
    const newData = data.table.concat();
    if (data.active !== name) {
      newData.sort((a, b) => (a[name] < b[name] ? 1 : -1));
      this.setState({
        [currenTable]: {
          table: newData,
          active: name,
          sort: 'asc',
        },
      });
    } else if (data.active === name && data.sort === 'asc') {
      newData.sort((a, b) => (b[name] < a[name] ? 1 : -1));
      this.setState({
        [currenTable]: {
          active: name,
          table: newData,
          sort: 'desc',
        },
      });
    } else {
      this.setState({
        [currenTable]: {
          table: newData,
          active: null,
          sort: null,
        },
      });
    }
  };
  render() {
    const { calend, divid } = this.state;
    return (
      <div className={styles.container}>
        {/* Таблица с дивидендами */}
        <div className={styles.table__wrapper}>
          <div className={styles.title}>
            <div className={styles.title__wrapper}>
              <h2 className={styles.title__text}>Дивиденды на 7 дней</h2>
              <a className={styles.calendar} href='#'>
                Календарь дивидендов
              </a>
            </div>
            <div className={styles.subtitle}>
              <img className={styles.subtitle__switch} src={switchIcon} alt='Switch' />
              <span className={styles.subtitle__text}>Голубые фишки</span>
              <img className={styles.subtitle__question} src={questionIcon} alt='Question' />
            </div>
          </div>
          <div className={styles.table}>
            <ul className={styles.table__header}>
              {dividendHeaderData.map((item) => (
                <li
                  onClick={this.clickHandler(item.connectedField, 'divid')}
                  className={cn(styles.header__item, styles.column, {
                    [styles['header__item-add']]:
                      item.connectedField === 'addField1' || item.connectedField === 'addField2',
                  })}
                  key={item.id}
                >
                  <span className={styles.item__text}>{item.title}</span>
                  <span
                    className={cn({
                      [styles.triangle__down]:
                        divid.sort === null ||
                        divid.sort === 'asc' ||
                        item.connectedField !== divid.active,
                      [styles.triangle__up]:
                        item.connectedField === divid.active && divid.sort === 'desc',
                      [styles.active__down]:
                        item.connectedField === divid.active && divid.sort === 'asc',
                      [styles.active__up]:
                        item.connectedField === divid.active && divid.sort === 'desc',
                    })}
                  />
                </li>
              ))}
            </ul>
            <ul className={styles.table__content}>
              {divid.table.map((item, index) => (
                <li className={styles.content__item} key={`${item.name}-${index}`}>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h4 className={styles.item__title}>{item.name}</h4>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Купить до</h5>
                    <p className={styles.field__value}>{item.buyBefore}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Закрытие реестра</h5>
                    <p className={styles.field__value}>{item.lastDate}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Размер купона</h5>
                    <p className={styles.field__value}>{item.couponValue}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Цена</h5>
                    <p className={styles.field__value}>{item.price}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                    <p className={styles.field__value}>{item.addField1}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                    <p className={styles.field__value}>{item.addField2}</p>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
        {/* ===================================================================
         Таблица с отчётами
         =================================================================== */}
        <div className={styles.table__wrapper}>
          <div className={styles.title}>
            <div className={styles.title__wrapper}>
              <h2 className={styles.title__text}>Отчётность на 7 дней</h2>
              <a className={styles.calendar} href='#'>
                Календарь отчётности
              </a>
            </div>
            <div className={styles.subtitle}>
              <img className={styles.subtitle__switch} src={switchIcon} alt='Switch' />
              <span className={styles.subtitle__text}>Голубые фишки</span>
              <img className={styles.subtitle__question} src={questionIcon} alt='Question' />
            </div>
          </div>
          <div className={styles.table}>
            <ul className={styles.table__header}>
              {reportHeaderData.map((item) => (
                <li
                  onClick={this.clickHandler(item.connectedField, 'calend')}
                  className={cn(styles.header__item, styles.column, {
                    [styles['header__item-add']]:
                      item.connectedField === 'addField1' || item.connectedField === 'addField2',
                  })}
                  key={item.id}
                >
                  <span className={styles.item__text}>{item.title}</span>
                  <span
                    className={cn({
                      [styles.triangle__down]:
                        calend.sort === null ||
                        calend.sort === 'asc' ||
                        item.connectedField !== calend.active,
                      [styles.triangle__up]:
                        item.connectedField === calend.active && calend.sort === 'desc',
                      [styles.active__down]:
                        item.connectedField === calend.active && calend.sort === 'asc',
                      [styles.active__up]:
                        item.connectedField === calend.active && calend.sort === 'desc',
                    })}
                  />
                </li>
              ))}
            </ul>
            <ul className={styles.table__content}>
              {calend.table.map((item, index) => (
                <li className={styles.content__item} key={`${item.name}-${index}`}>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h4 className={styles.item__title}>{item.name}</h4>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Дата отчёта</h5>
                    <p className={styles.field__value}>{item.reportDate}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Вид отчёта</h5>
                    <p className={styles.field__value}>{item.reportType}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Прогноз</h5>
                    <p className={styles.field__value}>{item.forecast}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column)}>
                    <h5 className={styles.field__name}>Факт</h5>
                    <p className={styles.field__value}>{item.fact}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                    <p className={styles.field__value}>{item.addField1}</p>
                  </div>
                  <div className={cn(styles.item__field, styles.column, styles['item__field-add'])}>
                    <p className={styles.field__value}>{item.addField2}</p>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
export default EventCalendar;
